import "./AddItem.css";
import { useState, useEffect } from "react";

const AddItem = ({ produto }) => {
  const [quant, setQuant] = useState(1);
  const [vUnitario, setvUnitario] = useState(20.99);
  const [total, setTotal] = useState(quant * vUnitario);

  useEffect(() => {
    CalculaTotal();
  }, [quant]);

  const SomaQuant = () => {
    setQuant(quant + 1);
  };

  const DiminuiQuant = () => {
    if (quant > 1) {
      setQuant(quant - 1);
    }
  };
  const CalculaTotal = () => {
    let tt = quant * vUnitario;
    setTotal(tt);
  };

  return (
    <>
      <div className="corpo">
        <div className="Nome">Duplo Bacon Quarteir√£o</div>
        <img
          className="img-produto"
          alt="imagem do produto"
          src="https://x8ki-letl-twmt.n7.xano.io/vault/NBLD8Bh8/JGnWkZw0/JM9Bkw../lanche-bacon.jpg"
        />
        <div className="ingredientes">
          P√£o, hamburguer, queijo cheddar,molho especial, bacon, alface e tomate
        </div>
        <div className="div-obs">
          <p className="lbl-obs">Observa√ß√µes:</p>
          <textarea
            className="text-obs"
            placeholder="caprichar na maionese..."
          ></textarea>
        </div>
        <div className="add-container">
          <div className="div-input-quantidade">
            <button className="btQuant" onClick={DiminuiQuant}>
              -
            </button>
            <input disabled className="input-quant" type="text" value={quant} />
            <button className="btQuant" onClick={SomaQuant}>
              +
            </button>
          </div>
          <div className="div-bt-add">
            <button className="bt-adicionar">
              Adicionar R${" "}
              {total.toLocaleString(undefined, { minimumFractionDigits: 2 })}
            </button>
          </div>
        </div>
      </div>
    </>
  );
};

export default AddItem;
